import loginTemp from '@/components/login'
import inputCell from '@/components/input-cell'
import { login } from '@/api'
import { verifyPhoneNumber } from '@/common/js/common.js'
export default {
	data() {
		return {
			phone: '',
			pwd: ''
		}
	},
	onLoad() {
		
	},
	methods: {
		async onLoginEvent () {
			if(!verifyPhoneNumber(this.phone)) {
				return;
			}
			if(this.pwd == "") {
				uni.showToast({
					title: '请输入密码',
					icon: 'none'
				})
				return;
			}
			let result = await login ({
				account: this.phone,
				password: this.pwd
			})
			if(result.code == 200) {
				console.log(result)
				uni.setStorage({
					key: 'id',
					data: result.data.userinfo.id,
				})
				uni.setStorage({
					key: 'user_id',
					data: result.data.userinfo.user_id,
				})
				uni.setStorage({
					key: 'token',
					data: result.data.userinfo.token,
				})
				uni.reLaunch({
					url: '/pages/user/home/index/index'
				})
			}	
		}
	},
	components: {
		inputCell,
		loginTemp
	}
}
